.create-account-container {
    width: 100%;
    max-width: 400px;
    margin: 0 auto;
    padding: 20px;
    background: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
  
  .title {
    font-size: 24px;
    font-weight: bold;
    color: #000;
  }
  
  .subtitle {
    font-size: 14px;
    color: #777;
    margin-bottom: 20px;
  }
  
  .form input {
    width: 100%;
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 14px;
  }
  
  .create-account-btn {
    width: 100%;
    padding: 10px;
    background: linear-gradient(90deg, #4a90e2, #007aff);
    border: none;
    color: #fff;
    font-size: 16px;
    font-weight: bold;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .create-account-btn:hover {
    background: linear-gradient(90deg, #007aff, #4a90e2);
  }
  /* Make sure the body and html cover the full height of the page */
html, body, .container {
    margin: 0;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start;  /* Aligns to the top */
  }
  
  .top-middle-image {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    max-width: 100%;  /* Optional: to prevent the image from overflowing */
  }
  
  